<template>
  <div class="main__content content">
    <div class="main__date"><div class="main__date-wrapper"><span>{{$store.state.dateTime}} Irkutsk time</span></div></div>
    <div class="main__title">{{$route.name !== 'home' ? $route.name : ''}}</div>
    <div class="content__wrapper">
      <keep-alive>
        <component :is="$route.meta.innerComponent"></component>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import TheAboutMe from './TheAboutMe'
import TheContact from './TheContact'
import TheEducation from './TheEducation'
import TheSkills from './TheSkills'
import TheWorkExperience from './TheWorkExperience'
import TheHome from './TheHome'

export default {
  components: {
    TheAboutMe,
    TheContact,
    TheEducation,
    TheSkills,
    TheWorkExperience,
    TheHome
  },
  mounted() {
    this.$store.commit('getDateTime')
    this.$store.dispatch('getDateTimeAsync')
  }
}
</script>