<template>
  <div id="app-canvas"></div>
</template>

<script>
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader'
import { AsciiEffect } from 'three/examples/jsm/effects/AsciiEffect'

export default {
  methods: {
    loadThree() {
      const scene = new THREE.Scene()

      const light = new THREE.PointLight(0xffffff)
      light.position.set(500, 500, 500)
      scene.add(light)

      const content = document.querySelector('#app-canvas')
      const camera = new THREE.PerspectiveCamera(75, content.offsetWidth / content.offsetHeight, 0.1, 1000)
      camera.position.set(1.8, 1.4, 4)

      const renderer = new THREE.WebGLRenderer()
      renderer.setClearColor(0xeeeeee)
      renderer.setSize(content.offsetWidth, content.offsetHeight)

      const effect = new AsciiEffect(renderer);
      effect.setSize(content.offsetWidth*0.9, content.offsetHeight*0.95);
      content.appendChild(effect.domElement);

      const controls = new OrbitControls(camera, renderer.domElement)
      controls.enableDamping = true
      controls.enableRotate = false
      controls.enableZoom = false
      controls.target.set(0, 1, 0)

      const fbxLoader = new FBXLoader()
      fbxLoader.load(
        './pc.fbx',
        (object) => {
          object.scale.set(.05, .05, .05)
          scene.add(object)
          if (window.innerWidth >= 768) {
            document.addEventListener('mousemove', (e) => {
              object.rotation.y = 2 * Math.PI * e.clientX / window.innerWidth / 4 - 0.4
            })
          }
        }
      )

      window.addEventListener('resize', onWindowResize, false)
      function onWindowResize() {
        camera.aspect = content.offsetWidth / content.offsetHeight
        camera.updateProjectionMatrix()
        renderer.setSize(content.offsetWidth, content.offsetHeight)
        effect.setSize(content.offsetWidth*0.9, content.offsetHeight*0.95);
      }

      function animate() {
        requestAnimationFrame(animate)
        controls.update()
        render()
      }

      function render() {
        controls.update();
        effect.render(scene, camera)
      }

      animate()
    }
  },
  mounted() {
    this.loadThree()
  }
}
</script>