import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import store from '../store/index'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      date: '12/04/2022 13:09',
      innerComponent: 'TheHome'
    }
  },
  {
    path: '/about_me',
    name: 'about_me',
    component: () => import('../views/InnerView.vue'),
    meta: {
      date: '14/04/2022 18:32',
      innerComponent: 'TheAboutMe'
    }
  },
  {
    path: '/work_experience',
    name: 'work_experience',
    component: () => import('../views/InnerView.vue'),
    meta: {
      date: '14/04/2022 15:12',
      innerComponent: 'TheWorkExperience'
    }
  },
  {
    path: '/education',
    name: 'education',
    component: () => import('../views/InnerView.vue'),
    meta: {
      date: '14/04/2022 15:19',
      innerComponent: 'TheEducation'
    }
  },
  {
    path: '/skills',
    name: 'skills',
    component: () => import('../views/InnerView.vue'),
    meta: {
      date: '14/04/2022 19:00',
      innerComponent: 'TheSkills'
    }
  },
  {
    path: '/contact',
    name: 'contact',
    component: () => import('../views/InnerView.vue'),
    meta: {
      date: '14/04/2022 15:48',
      innerComponent: 'TheContact'
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.afterEach((to, from) => {
  store.commit('setSelectedIdx', 0)
  if (to.fullPath === '/') {
    store.commit('setInfo', {name: 'about_me', date: router.resolve({name: 'about_me'}).meta.date})
  } else {
    store.commit('setInfo', {name: 'up', date: router.resolve({name: 'home'}).meta.date})
  }
})

export default router
