import { createStore } from 'vuex'

export default createStore({
  state: {
    selectedIdx: 0,
    info: {
      name: '-',
      date: '-'
    },
    dateTime: ''
  },
  mutations: {
    setSelectedIdx(state, idx) {
      state.selectedIdx = idx
    },
    setInfo(state, payload) {
      state.info.name = payload.name !== 'home' ? payload.name : 'up'
      state.info.date = payload.date
    },
    getDateTime(state) {
      const currentDate = new Date(new Date().toLocaleString("en-US", {timeZone: "Asia/Irkutsk"}))
      state.dateTime = 
        ("0" + currentDate.getDate()).slice(-2) + '/' + 
        ("0" + (currentDate.getMonth()+1)).slice(-2) + '/' +
        currentDate.getFullYear() + " " +
        ("0" + currentDate.getHours()).slice(-2) + ":" +
        ("0" + currentDate.getMinutes()).slice(-2)
    }
  },
  actions: {
    getDateTimeAsync(context) {
      setInterval(() => {
        context.commit('getDateTime')
      }, 1000)
    }
  }
})
